export default {
  data(){
    return{
      //导航数据
      nav_data:[
        {
          text:"漫画",
          router:"update",
        },
        {
          text:"首页",
          router:"homepage",
        },
        {
          text:"充值记录",
          router:"充值记录",
        },
      ],
    }
  },
  methods:{
    nav_router(v){
      if(v['router']=='充值记录'){
        // 传递事件
        this.$emit('fill-record','充值记录')
      }
      else{
        this.$router.push({name:v['router']})
      }
    }
  },
}
